import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';

import suscribeIcon from '../images/icons/subscribe.svg';
import ccIcon from '../images/icons/license/cc.svg';
import byIcon from '../images/icons/license/by.svg';
import saIcon from '../images/icons/license/sa.svg';

import content from '../../site/content/worksystems.json';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    allMarkdownRemark: {
      nodes: {
        frontmatter: {
          title: string;
          description: string;
          thumbnail: string;
        };
        fields: {
          localizedSlug: string;
        };
      }[];
    };
  };
}

const Worksystems = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const worksystems = data.allMarkdownRemark.nodes;
  return (
    <>
      <SEO
        title={content[locale].seo.title}
        description={content[locale].seo.description}
        image={content[locale].seo.image}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div className="container py-32 md:py-36">
          <h1 className="text-5xl sm:text-6xl md:text-9xl text-white">{content[locale].title}</h1>
          <p className="text-grey">{content[locale].description}</p>
          <div className="py-8 md:py-16">
            {worksystems.map(worksystem => (
              <div className="py-8">
                <div>
                  <img src={worksystem.frontmatter.thumbnail} className="icon-white mx-auto h-24" />
                </div>
                <div className="text-center pt-1 pb-4">
                  <h2 className="text-3xl text-white font-bold">{worksystem.frontmatter.title}</h2>
                  <p className="text-grey">{worksystem.frontmatter.description}</p>
                </div>
                <Link to={worksystem.fields.localizedSlug}>
                  <div className="bg-orange flex max-w-max px-4 py-2 rounded mx-auto transition duration-500 ease-in-out transform hover:scale-125">
                    <img src={suscribeIcon} className="icon-white w-6" />
                  </div>
                </Link>
              </div>
            ))}
          </div>
          <p className="text-grey text-xs text-center">
            Collaborative Worksystems for Decentralized Communities by Exorde Labs is licensed under
            <a
              href="https://creativecommons.org/licenses/by-sa/4.0/?ref=chooser-v1"
              target="_blank"
              rel="noopener noreferrer"
              className="font-bold px-1"
            >
              CC BY-SA 4.0
              <div className="inline-flex">
                <img src={ccIcon} className="icon-grey w-4 mx-1" />
                <img src={byIcon} className="icon-grey w-4 mr-1 " />
                <img src={saIcon} className="icon-grey w-4" />
              </div>
            </a>
          </p>
        </div>
      </Layout>
    </>
  );
};

export default Worksystems;

export const query = graphql`
  query ($locale: String!) {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/worksystems/" }, fields: { locale: { eq: $locale } } }
      sort: { fields: frontmatter___order, order: ASC }
    ) {
      nodes {
        frontmatter {
          title
          description
          thumbnail
        }
        fields {
          localizedSlug
        }
      }
    }
  }
`;
